	import route from "@/common/public.js"
	import Vue from 'vue'
	import lazy from "@/common/vue-lazyload.js"
	Vue.use(lazy)
	export default{
		data(){
			return{
				imgUrl :'',  //图片访问ip
				goodsname:"", //拼接后的完整地址
				datalist:[],//缓存对象
				hash_list:[],//首次加载显示数据
				isExist:0,//判断关键词是否存在，-1存在,0为空值，1不存在，默认为0
				
				title: 'product-list',
                productList: [],
                renderImage: false
			}
		},
		methods:{
			ipt_keyword:function(evt){
				this.goodsname=evt.target.value;
				this.isExist=this.check_exist_keyword(uni.getStorageSync('search_list'),(evt.target.value).trim());
				if(this.isExist==-1){
					console.log('该词已经存在!')
					return false;
				}else if((evt.target.value).trim()==''){
					console.log('请输入您需要查找的内容！')
					return false;
				}else{
					if(uni.getStorageSync('search_list')){
						this.datalist=uni.getStorageSync('search_list');
					}
					this.datalist.push(evt.target.value.trim());
					try {
						uni.setStorageSync('search_list', this.datalist);
					} catch (e) {
						console.log('没有缓存起')// error
					}
				}
				
			},
			
			check_exist_keyword:function(arr,val){
				for (var i = 0; i < arr.length; i++) {
					if(arr[i]==val){
						return this.isExist= -1;
					}else{
						this.isExist= 1;
					}
				}
			},
			clear_keyword:function(){
				try {
					uni.showModal({
						title: '提示',
						content: '是否确认清除搜索记录',
						success: function (res) {
							if (res.confirm) {
								uni.removeStorageSync('search_list');
								uni.redirectTo({
									url: "../index/search"
								})
							} else if (res.cancel) {
								
							}
						}
					});
				} catch (e) {
					// error
				}
			},
			history_btn_search(val){
				this.goodsname=val;
				this.loadData();
				setTimeout(() => {
				    this.renderImage = true;
				}, 300);
			},
			btn_search(evt){
				console.log(this)
				if(this.goodsname == ''){
					uni.showToast({
						title: '请输入您需要查找的内容',
						icon: 'none'
					})
					return false;
				}else{
					this.hash_list = uni.getStorageSync('search_list')
					this.loadData();
					setTimeout(() => {
					    this.renderImage = true;
					}, 300);
				}
			},
			loadData(action = 'add') {
				uni.request({
						url:route.variable+'/mobile/index/ProductSearch',
						method:'GET',
						data:{
							goodsname: this.goodsname,
						},
						success: (res) => {
							console.log(res);
							if (res.data.status==0){
								this.productList = [];
								res.data.data.forEach(item => {
								    this.productList.push(item);
								});
							} else{
								uni.showToast({
									title: res.data.message+'，请重新输入',
									icon:'none',
									duration: 3000
								})
							}
						}
					})  
            },
		},
		onLoad() {
			this.imgUrl = route.variable;  //图片访问IP
            this.hash_list=uni.getStorageSync('search_list');
        },
		onReady() {
			
		},
		 onPullDownRefresh() {// 下拉事件
		 let _this = this;
		 setTimeout(function() {
			 _this.productList = [];
		 },2900)
			setTimeout(function() {
				uni.startPullDownRefresh({
					success:function() {
						_this.loadData();
						uni.showToast({
							title:'数据已刷新',
							icon:'none'
						});
						setTimeout(() => {
						    _this.renderImage = true;
						}, 300);
					},
					fail:item=> {
						uni.showToast({
							title:'获取失败',
							icon:'none'
						})
					}
				})
			},3000)
		},

	}
